:root {
  --brand: #2874f0;           /* Flipkart-ish blue */
  --brand-dark: #0f5ad6;
  --bg: #f5f6f8;
  --card: #ffffff;
  --text: #1f2937;
  --muted: #6b7280;
  --border: #e5e7eb;
  --success: #10b981;
  --danger: #ef4444;
}

* { box-sizing: border-box; }

.contact-page {
  background: var(--bg);
  min-height: calc(100vh - 120px);
  padding: 24px;
}

.contact-hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(180deg, #eef4ff, #ffffff);
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 20px 24px;
  margin: 0 auto 18px;
  max-width: 1100px;
}

.contact-hero h1 {
  margin: 0;
  font-size: 28px;
  color: var(--text);
}

.contact-hero p {
  margin: 6px 0 0;
  color: var(--muted);
}

.meta {
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--muted);
  font-size: 14px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 12px;
  display: inline-block;
}
.dot.online { background: #22c55e; }

.contact-grid {
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  gap: 18px;
  max-width: 1100px;
  margin: 0 auto;
}

.card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 6px 20px rgba(0,0,0,0.04);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 16px;
  border-bottom: 1px solid var(--border);
}

.card-header .title {
  font-weight: 700;
  color: var(--text);
  display: flex;
  align-items: center;
  gap: 10px;
}

.icon {
  font-size: 18px;
}

.badge {
  background: var(--brand);
  color: #fff;
  font-size: 12px;
  padding: 6px 10px;
  border-radius: 999px;
  font-weight: 600;
}
.badge.outline {
  background: #eef4ff;
  color: var(--brand);
  border: 1px solid var(--brand);
}

/* Chat */
.chat-card {
  display: flex;
  flex-direction: column;
}

.chat-window {
  height: 360px;
  overflow-y: auto;
  padding: 16px;
  background: #fafbff;
}

.bubble {
  max-width: 74%;
  margin-bottom: 10px;
  padding: 10px 12px;
  border-radius: 14px;
  line-height: 1.4;
  font-size: 14.5px;
}

.bubble.bot {
  background: #eef4ff;
  color: #1e293b;
  border: 1px solid #dbe6ff;
  border-top-left-radius: 4px;
}

.bubble.me {
  margin-left: auto;
  background: var(--brand);
  color: #fff;
  border-top-right-radius: 4px;
}

.quick-topics {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding: 10px 14px 0;
}

.chip {
  border: 1px solid var(--border);
  background: #fff;
  border-radius: 999px;
  padding: 6px 12px;
  font-size: 13px;
  cursor: pointer;
  color: var(--text);     /* always visible */
  opacity: 1;             /* always visible */
  transition: all 0.2s ease;
}
.chip:hover { border-color: var(--brand); color: var(--brand); }

.chat-input {
  display: flex;
  gap: 10px;
  padding: 12px 14px 16px;
}
.chat-input input {
  flex: 1;
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 10px 12px;
  font-size: 14px;
}
.chat-input button {
  background: var(--brand);
  border: none;
  color: #fff;
  padding: 0 px;  /* ⬅ wider Send button */
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  font-size: 14.5px;
}
.chat-input button:hover { background: var(--brand-dark); }

.transfer-note {
  padding: 8px 14px 16px;
  font-size: 13px;
  color: var(--muted);
}

/* Call request */
.call-card .form {
  padding: 14px 16px 6px;
}
.form label {
  display: block;
  font-size: 13px;
  color: var(--text);
  margin-bottom: 10px;
}
.form input,
.form select,
.form textarea {
  width: 100%;
  margin-top: 6px;
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 10px 12px;
  font-size: 14px;
  outline: none;
}
.form textarea { resize: vertical; }

.form .primary {
  width: 100%;
  margin-top: 10px;
  background: var(--brand);
  color: #fff;
  border: none;
  border-radius: 10px;
  padding: 12px;
  font-weight: 700;
  cursor: pointer;
}
.form .primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.form .primary:hover:not(:disabled) { background: var(--brand-dark); }

.status {
  margin: 8px 16px 0;
  font-size: 13.5px;
  color: var(--text);
}
.status:before {
  content: "• ";
  color: var(--success);
  font-weight: 900;
}
.call-hint {
  padding: 8px 16px 16px;
  color: var(--muted);
  font-size: 12.5px;
}

/* FAQ */
.faq {
  max-width: 1100px;
  margin: 20px auto 0;
}
.faq h2 {
  font-size: 20px;
  margin: 0 0 10px;
  color: var(--text);
}
.faq-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 14px;
}
.faq-card {
  background: #fff;
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 14px;
}
.faq-title {
  font-weight: 700;
  margin-bottom: 6px;
  color: var(--text);
}
.faq-desc {
  color: var(--muted);
  font-size: 14px;
  margin-bottom: 8px;
}
.link-btn {
  border: none;
  background: transparent;
  color: var(--brand);
  font-weight: 700;
  cursor: pointer;
  padding: 0;
}
.link-btn:hover { text-decoration: underline; }

/* Responsive */
@media (max-width: 960px) {
  .contact-grid { grid-template-columns: 1fr; }
  .chat-window { height: 320px; }
  .faq-grid { grid-template-columns: 1fr 1fr; }
}
@media (max-width: 640px) {
  .faq-grid { grid-template-columns: 1fr; }
}
