/* Container for the Join Us form */
.joinus-container {
  max-width: 500px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fafafa;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Role Selector Button Styling */
.role-selector button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.role-selector button:hover {
  background-color: #45a049;
}

/* Input and Select Styling */
input, select {
  display: block;
  width: 100%;
  margin: 10px 0;
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

input:focus, select:focus {
  outline: none;
  border-color: #4CAF50; /* Green border when focused */
  box-shadow: 0 0 8px rgba(76, 175, 80, 0.2); /* Soft green glow */
}

/* Submit Button Styling */
.submit-button {
  margin-top: 20px;
  padding: 12px 25px;
  font-size: 18px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #45a049;
}

/* Responsive Design for Smaller Screens */
@media (max-width: 600px) {
  .joinus-container {
    padding: 15px;
  }

  input, select {
    padding: 10px;
  }

  .submit-button {
    padding: 10px 20px;
  }

  .role-selector button {
    padding: 8px 16px;
  }
}
