.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #22a2a7;
  padding: 15px 30px;
  box-shadow: 0 2px 6px rgb(53, 174, 219);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar-logo {
  font-size: 1.5rem;
  color: #ffffff;
  font-weight: bold;
}

.navbar-links {
  display: flex;
  align-items: center;
}

.navbar-links a {
  color: #f3f4f6;
  margin-left: 20px;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s ease, transform 0.2s ease;
}

.navbar-links a:hover {
  color: #a4bedd;
}

.hamburger-icon {
  display: none;
  color: white;
  font-size: 1.75em;
  cursor: pointer;
}

.hamburger-icon .line {
  width: 25px;
  height: 3px;
  background-color: white;
  margin: 4px;
}

/* Dropdown styles */
.dropdown {
  position: relative;
  display: inline-block;
  margin-left: 20px;
}

.navbar-profile-pic {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 2px solid #fff;
  object-fit: cover;
  cursor: pointer;
}

.dropdown-content {
  display: none; /* hidden by default */
  position: absolute;
  background-color: #1b9ca1;
  min-width: 200px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1001;
  right: 0;
  border-radius: 6px;
  overflow: hidden;
  flex-direction: column;
}

.dropdown-content.show {
  display: flex; /* controlled by isDropdownOpen */
}

.dropdown-content a {
  color: white;
  padding: 10px 16px;
  display: block;
  text-decoration: none;
}

.dropdown-content a:hover {
  background-color: #15787d;
}

.dropdown-divider {
  height: 1px;
  background-color: #ffffff40;
  margin: 5px 0;
}

.dropdown-content button {
  background: none;
  border: none;
  color: white;
  padding: 10px 16px;
  width: 100%;
  text-align: left;
  font-size: 1rem;
  font-family: inherit;
  cursor: pointer;
}

.dropdown-content button:hover {
  background-color: #15787d;
}

/* Mobile styles */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .navbar-heading {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .hamburger-icon {
    display: block;
    margin-top: 5px;
  }

  .navbar-links {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-top: 10px;
    animation: slideDown 0.3s ease-in-out;
  }

  .navbar-links.open {
    display: flex;
  }

  .navbar-links a,
  .navbar-profile-pic {
    margin: 10px 20px;
    font-size: 1.3rem;
    width: 100%;
    padding: 8px 0;
    text-align: left;
  }

  .dropdown-content {
    position: static;
    background-color: #1b9ca1;
    box-shadow: none;
    border-radius: 0;
  }

  .dropdown-content a,
  .dropdown-content button {
    background-color: transparent;
    padding: 10px 20px;
    color: #f3f4f6;
    font-size: 1.2rem;
  }

  .dropdown-divider {
    width: 100%;
    background-color: #ffffff33;
  }

  @keyframes slideDown {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
}
