.about-container {
  padding: 2rem 4rem;
  font-family: 'Segoe UI', sans-serif;
}

.feedback-link {
  text-align: right;
  margin-bottom: 1rem;
}

.feedback-link a {
  background-color: #22a2a7;
  color: white;
  padding: 10px 20px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.3s;
}

.feedback-link a:hover {
  background-color: #15787d;
}

.about-text h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.about-text p {
  font-size: 1.1rem;
  line-height: 1.6;
}

.about-updates {
  margin-top: 3rem;
}

.about-updates h2 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}

.updates-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.update-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 1rem;
  background-color: #f9f9f9;
  text-align: center;
}

.update-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 0.5rem;
}
