.admin-updates-container {
  max-width: 600px;
  margin: 2rem auto;
  padding: 2rem;
  background: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.admin-updates-container h1 {
  text-align: center;
  margin-bottom: 1.5rem;
}

.update-form label {
  display: block;
  margin: 0.5rem 0;
  font-weight: bold;
}

.update-form textarea {
  width: 100%;
  height: 100px;
  padding: 8px;
  border-radius: 6px;
  border: 1px solid #ccc;
  resize: vertical;
}

.update-form input[type="file"] {
  margin-top: 0.5rem;
}

.update-form button {
  margin-top: 1rem;
  padding: 10px 20px;
  background-color: #22a2a7;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s;
}

.update-form button:hover {
  background-color: #15787d;
}

.status {
  margin-top: 1rem;
  text-align: center;
  font-weight: bold;
}
