/* Centering the form */
.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f8f9fa;
}

/* Box styling */
.auth-box {
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 350px;
  text-align: center;
}

/* Input fields */
.auth-box input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* Button styling */
.auth-box button {
  width: 100%;
  padding: 10px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.auth-box button:hover {
  background-color: #333;
}

/* Link styling */
.auth-box p {
  margin-top: 10px;
}

.auth-box a {
  color: blue;
  text-decoration: none;
  font-weight: bold;
}

.auth-box a:hover {
  text-decoration: underline;
}

/* Logo styling */
.auth-logo {
  width: 100px;
  height: auto;
  display: block;
  margin: 0 auto 20px auto;
}
.password-strength {
  font-size: 14px;
  margin-top: 5px;
  text-align: left;
}

.password-strength.weak {
  color: red;
}

.password-strength.medium {
  color: orange;
}

.password-strength.strong {
  color: green;
}
