.admin-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #22a2a7;
  padding: 15px 30px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.admin-navbar-logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #fff;
  text-decoration: none;
}

.admin-navbar-links {
  display: flex;
  align-items: center;
  gap: 20px;
}

.admin-navbar-links a,
.admin-navbar-links button {
  color: #f3f4f6;
  text-decoration: none;
  font-size: 1rem;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px 12px;
  transition: color 0.3s ease;
}

.admin-navbar-links a:hover,
.admin-navbar-links button:hover {
  color: #dbeafe;
}

/* Optional: Responsive improvements */
@media (max-width: 768px) {
  .admin-navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .admin-navbar-links {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin-top: 10px;
  }
}
